.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 50px;
}

.js-input {
  padding: 5px 10px;
  font-weight: bold;
  border-radius: 10px;
  border: none;
  font-size: 15px;
  margin-bottom: 15px;
}

.submit__btn {
  border-radius: 10px;
  padding: 8px 10px;
  margin-left: 20px;
  font-weight: bold;
  font-size: 15px;
  background-color: gold;
  color: rgb(41, 100, 2);
  border: none;
  transition: 0.6s;
}

.submit__btn:hover {
  background-color: rgb(172, 154, 54);
  color: rgb(0, 0, 0);
}